"use client";
import useAuthStore from "@/store/MyAuthState";
import axios from "axios";
import { useEffect, useState } from "react";

export function useFetchData(apiEndpoint: string) {
  const MyAuthState = useAuthStore();
  const [data, setData] = useState<any[]>([]); // `any` o'rniga aniq tiplar qo'yishingiz mumkin
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    const fetchData = async () => {
      try {
        const res = await axios.get(`https://nt.softly.uz/api${apiEndpoint}`, {
          headers: { Authorization: `Bearer ${MyAuthState.token}` },
        });
        setData(res.data); // Olingan ma'lumotlarni saqlash
      } catch (e: any) {
        setError(e.message); // Xatolikni saqlash
        console.error(e);
      }
    };

    fetchData(); // Ma'lumotlarni olish
  }, [apiEndpoint, MyAuthState.token]); // `apiEndpoint` va token o'zgarganda qaytadan chaqiriladi

  return { data, error };
}

